.piece{
    width: calc(100%/8);
    height: calc(100%/8);
    position: absolute;
    top: 0;
    left: 0;
    outline: none;
}

.piece:hover{
    cursor: grab;
}

.piece:active{
    cursor: grabbing;
}

.highlight{
    width: calc(100%/8);
    height: calc(100%/8);
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgb(255, 255, 0);
    opacity: 0.5;
}

.hint{
    width: calc(100%/8);
    height: calc(100%/8);
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.2);
    background-clip: content-box;
    border-radius: 50%;
    box-sizing: border-box;
    /*pointer-events: none;*/
    padding: 4.2%;
}

.capture-hint{
    width: calc(100%/8);
    height: calc(100%/8);
    position: absolute;
    top: 0;
    left: 0;
    background-clip: content-box;
    border-radius: 50%;
    box-sizing: border-box;
    pointer-events: none;
    border-width: 6.1px;
    border: 5px solid rgba(0,0,0,.2);
}

